'use client';

import { useState } from 'react';

interface Company {
  companyId: number;
  companyName: string;
}

interface MedicineItem {
  id: string;
  itemCode: string;
  medicineName: string;
  companyId: number;
  company: string;
  genericName: string;
  gst: string;
  billCode: string;
  itemShort: string;
  rack: string;
  shelf: string;
  typeId: number;
  type: string;
  ingredients: string;
  cess: string;
  discount: string;
  rol: string;
  packItem: string;
  hsnCode: string;
  purchaseRate: string;
  salesRate: string;
  lastBill: string;
  lastPurchase: string;
  quantity: string;
}

export default function AddMedicine() {
  const [companies, setCompanies] = useState<Company[]>([]);
  const [types, setTypes] = useState<string[]>(['AMPOULE', 'TABLET', 'CAPSULE', 'SYRUP', 'POWDER']);
  const [isCompanyModalOpen, setIsCompanyModalOpen] = useState(false);
  const [isTypeModalOpen, setIsTypeModalOpen] = useState(false);
  const [newCompanyName, setNewCompanyName] = useState('');
  const [newTypeName, setNewTypeName] = useState('');
  const [isEditing, setIsEditing] = useState(false);

  const [formData, setFormData] = useState<MedicineItem>({
    id: '',
    itemCode: '',
    medicineName: '',
    companyId: 0,
    company: '',
    genericName: '',
    gst: '',
    billCode: '',
    itemShort: '',
    rack: '',
    shelf: '',
    typeId: 0,
    type: '',
    ingredients: '',
    cess: '',
    discount: '',
    rol: '',
    packItem: '',
    hsnCode: '',
    purchaseRate: '',
    salesRate: '',
    lastBill: '',
    lastPurchase: '',
    quantity: '',
  });

  const handleInputChange = (field: keyof MedicineItem, value: string | number) => {
    setFormData((prev) => ({
      ...prev,
      [field]: value,
    }));
  };

  const handleAddCompany = () => {
    if (newCompanyName.trim()) {
      const newId = companies.length > 0 ? Math.max(...companies.map((c) => c.companyId)) + 1 : 1;
      const newCompany: Company = {
        companyId: newId,
        companyName: newCompanyName.trim(),
      };
      setCompanies([...companies, newCompany]);
      setFormData((prev) => ({
        ...prev,
        companyId: newCompany.companyId,
        company: newCompany.companyName,
      }));
      setNewCompanyName('');
      setIsCompanyModalOpen(false);
    }
  };

  const handleAddType = () => {
    if (newTypeName.trim()) {
      const newType = newTypeName.trim().toUpperCase();
      setTypes([...types, newType]);
      setFormData((prev) => ({ ...prev, type: newType }));
      setNewTypeName('');
      setIsTypeModalOpen(false);
    }
  };

  const handleSave = () => {
    if (isEditing) {
      console.log('Updating medicine:', formData);
    } else {
      const newMedicine = { ...formData, id: Date.now().toString() };
      console.log('Adding new medicine:', newMedicine);
    }
  };

  const handleDelete = () => {
    if (isEditing) {
      console.log('Deleting medicine with ID:', formData.id);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 p-8">
      <div className="mx-auto max-w-7xl">
        {/* Header */}
        <header className="mb-8 text-center">
          <h1 className="text-3xl font-bold text-gray-900">Add Medicine</h1>
          <p className="mt-2 text-sm text-gray-500">Hospital Pharmacy Management System</p>
        </header>

        {/* Main Form */}
        <div className="rounded-xl border border-gray-100 bg-white p-8 shadow-lg">
          <div className="grid grid-cols-1 gap-8 lg:grid-cols-2">
            {/* Left Column */}
            <div className="space-y-6">
              <h3 className="text-xl font-semibold text-gray-800">Basic Information</h3>
              <div className="space-y-4">
                {/* Code */}
                <div className="flex items-center gap-4">
                  <label className="w-32 text-sm font-medium text-gray-700">Code</label>
                  <input
                    type="text"
                    value={formData.itemCode}
                    onChange={(e) => handleInputChange('itemCode', e.target.value)}
                    className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                    placeholder="Enter medicine code"
                  />
                </div>

                {/* Bill Code & Item Short */}
                <div className="grid grid-cols-2 gap-4">
                  <div className="flex items-center gap-4">
                    <label className="w-32 text-sm font-medium text-gray-700">Bill Code</label>
                    <input
                      type="text"
                      value={formData.billCode}
                      onChange={(e) => handleInputChange('billCode', e.target.value)}
                      className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                      placeholder="Enter bill code"
                    />
                  </div>
                  <div className="flex items-center gap-4">
                    <label className="w-32 text-sm font-medium text-gray-700">Item Short</label>
                    <input
                      type="text"
                      value={formData.itemShort}
                      onChange={(e) => handleInputChange('itemShort', e.target.value)}
                      className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                      placeholder="Enter short name"
                    />
                  </div>
                </div>

                {/* Name */}
                <div className="flex items-center gap-4">
                  <label className="w-32 text-sm font-medium text-gray-700">Medicine Name</label>
                  <input
                    type="text"
                    value={formData.medicineName}
                    onChange={(e) => handleInputChange('medicineName', e.target.value)}
                    className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                    placeholder="Enter medicine name"
                  />
                </div>

                {/* Company */}
                <div className="flex items-center gap-4">
                  <label className="w-32 text-sm font-medium text-gray-700">Company</label>
                  <select
                    value={formData.companyId}
                    onChange={(e) => {
                      const selectedId = parseInt(e.target.value);
                      const selectedCompany = companies.find((c) => c.companyId === selectedId);
                      setFormData((prev) => ({
                        ...prev,
                        companyId: selectedId,
                        company: selectedCompany?.companyName || '',
                      }));
                    }}
                    className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                  >
                    <option value={0}>Select Company</option>
                    {companies.map((company) => (
                      <option key={company.companyId} value={company.companyId}>
                        {company.companyName}
                      </option>
                    ))}
                  </select>
                  <button
                    onClick={() => setIsCompanyModalOpen(true)}
                    className="rounded-lg bg-blue-600 px-4 py-2.5 text-sm font-medium text-white hover:bg-blue-700"
                  >
                    +
                  </button>
                </div>

                {/* Generic Name */}
                <div className="flex items-center gap-4">
                  <label className="w-32 text-sm font-medium text-gray-700">Generic Name</label>
                  <input
                    type="text"
                    value={formData.genericName}
                    onChange={(e) => handleInputChange('genericName', e.target.value)}
                    className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                    placeholder="Enter generic name"
                  />
                </div>

                {/* R.O.L & Pack Item */}
                <div className="grid grid-cols-2 gap-4">
                  <div className="flex items-center gap-4">
                    <label className="w-32 text-sm font-medium text-gray-700">R.O.L</label>
                    <input
                      type="text"
                      value={formData.rol}
                      onChange={(e) => handleInputChange('rol', e.target.value)}
                      className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                      placeholder="Enter R.O.L"
                    />
                  </div>
                  <div className="flex items-center gap-4">
                    <label className="w-32 text-sm font-medium text-gray-700">Pack Item</label>
                    <input
                      type="text"
                      value={formData.packItem}
                      onChange={(e) => handleInputChange('packItem', e.target.value)}
                      className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                      placeholder="Enter pack item"
                    />
                  </div>
                </div>

                {/* HSN Code */}
                <div className="flex items-center gap-4">
                  <label className="w-32 text-sm font-medium text-gray-700">HSN Code</label>
                  <input
                    type="text"
                    value={formData.hsnCode}
                    onChange={(e) => handleInputChange('hsnCode', e.target.value)}
                    className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                    placeholder="Enter HSN code"
                  />
                </div>

                {/* Purchase Rate & Sales Rate */}
                <div className="grid grid-cols-2 gap-4">
                  <div className="flex items-center gap-4">
                    <label className="w-32 text-sm font-medium text-gray-700">Purchase Rate</label>
                    <input
                      type="text"
                      value={formData.purchaseRate}
                      onChange={(e) => handleInputChange('purchaseRate', e.target.value)}
                      className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                      placeholder="Enter purchase rate"
                    />
                  </div>
                  <div className="flex items-center gap-4">
                    <label className="w-32 text-sm font-medium text-gray-700">Sales Rate</label>
                    <input
                      type="text"
                      value={formData.salesRate}
                      onChange={(e) => handleInputChange('salesRate', e.target.value)}
                      className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                      placeholder="Enter sales rate"
                    />
                  </div>
                </div>

                {/* Inventory Details */}
                <div className="rounded-lg bg-gray-100 p-6">
                  <div className="space-y-4">
                    <div className="grid grid-cols-2 gap-4">
                      <div className="flex items-center gap-4">
                        <label className="w-32 text-sm font-medium text-gray-700">Last Bill</label>
                        <input
                          type="text"
                          value={formData.lastBill}
                          onChange={(e) => handleInputChange('lastBill', e.target.value)}
                          className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                          placeholder="Enter last bill"
                        />
                      </div>
                      <div className="flex items-center gap-4">
                        <label className="w-32 text-sm font-medium text-gray-700">Last Purchase</label>
                        <input
                          type="text"
                          value={formData.lastPurchase}
                          onChange={(e) => handleInputChange('lastPurchase', e.target.value)}
                          className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                          placeholder="Enter last purchase"
                        />
                      </div>
                    </div>
                    <div className="flex items-center gap-4">
                      <label className="w-32 text-sm font-medium text-gray-700">Quantity</label>
                      <input
                        type="text"
                        value={formData.quantity}
                        onChange={(e) => handleInputChange('quantity', e.target.value)}
                        className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                        placeholder="Enter quantity"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>

            {/* Right Column */}
            <div className="space-y-6">
              <h3 className="text-xl font-semibold text-gray-800">Additional Details</h3>
              <div className="space-y-4">
                {/* Rack & Shelf */}
                <div className="grid grid-cols-2 gap-4">
                  <div className="flex items-center gap-4">
                    <label className="w-32 text-sm font-medium text-gray-700">Rack</label>
                    <input
                      type="text"
                      value={formData.rack}
                      onChange={(e) => handleInputChange('rack', e.target.value)}
                      className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                      placeholder="Enter rack"
                    />
                  </div>
                  <div className="flex items-center gap-4">
                    <label className="w-32 text-sm font-medium text-gray-700">Shelf</label>
                    <input
                      type="text"
                      value={formData.shelf}
                      onChange={(e) => handleInputChange('shelf', e.target.value)}
                      className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                      placeholder="Enter shelf"
                    />
                  </div>
                </div>

                {/* Type */}
                <div className="flex items-center gap-4">
                  <label className="w-32 text-sm font-medium text-gray-700">Type</label>
                  <select
                    value={formData.type}
                    onChange={(e) => handleInputChange('type', e.target.value)}
                    className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                  >
                    <option value="">Select Type</option>
                    {types.map((type) => (
                      <option key={type} value={type}>{type}</option>
                    ))}
                  </select>
                  <button
                    onClick={() => setIsTypeModalOpen(true)}
                    className="rounded-lg bg-blue-600 px-4 py-2.5 text-sm font-medium text-white hover:bg-blue-700"
                  >
                    +
                  </button>
                </div>

                {/* Ingredients */}
                <div className="flex items-center gap-4">
                  <label className="w-32 text-sm font-medium text-gray-700">Ingredients</label>
                  <input
                    type="text"
                    value={formData.ingredients}
                    onChange={(e) => handleInputChange('ingredients', e.target.value)}
                    className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                    placeholder="Enter ingredients"
                  />
                </div>

                {/* GST% & Cess */}
                <div className="grid grid-cols-2 gap-4">
                  <div className="flex items-center gap-4">
                    <label className="w-32 text-sm font-medium text-gray-700">GST %</label>
                    <input
                      type="text"
                      value={formData.gst}
                      onChange={(e) => handleInputChange('gst', e.target.value)}
                      className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                      placeholder="Enter GST %"
                    />
                  </div>
                  <div className="flex items-center gap-4">
                    <label className="w-32 text-sm font-medium text-gray-700">Cess</label>
                    <input
                      type="text"
                      value={formData.cess}
                      onChange={(e) => handleInputChange('cess', e.target.value)}
                      className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                      placeholder="Enter cess"
                    />
                  </div>
                </div>

                {/* Disc% */}
                <div className="flex items-center gap-4">
                  <label className="w-32 text-sm font-medium text-gray-700">Discount %</label>
                  <input
                    type="text"
                    value={formData.discount}
                    onChange={(e) => handleInputChange('discount', e.target.value)}
                    className="flex-1 rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
                    placeholder="Enter discount %"
                  />
                </div>
              </div>
            </div>
          </div>

          {/* Action Buttons */}
          <div className="mt-10 flex justify-center gap-4 border-t border-gray-200 pt-6">
            <button
              onClick={handleSave}
              className="rounded-lg bg-blue-600 px-8 py-3 text-sm font-medium text-white hover:bg-blue-700 shadow-md"
            >
              {isEditing ? 'Update' : 'Add'}
            </button>
            {isEditing && (
              <button
                onClick={handleDelete}
                className="rounded-lg bg-red-600 px-8 py-3 text-sm font-medium text-white hover:bg-red-700 shadow-md"
              >
                Delete
              </button>
            )}
          </div>
        </div>

        {/* Company Modal */}
        {isCompanyModalOpen && (
          <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/60">
            <div className="w-full max-w-md rounded-xl bg-white p-6 shadow-2xl">
              <h3 className="mb-4 text-lg font-semibold text-gray-800">Add New Company</h3>
              <input
                type="text"
                value={newCompanyName}
                onChange={(e) => setNewCompanyName(e.target.value)}
                placeholder="Enter company name"
                className="mb-4 w-full rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
              />
              <div className="flex justify-end gap-2">
                <button
                  onClick={() => setIsCompanyModalOpen(false)}
                  className="rounded-lg px-4 py-2 text-sm font-medium text-gray-600 hover:bg-gray-100"
                >
                  Cancel
                </button>
                <button
                  onClick={handleAddCompany}
                  className="rounded-lg bg-blue-600 px-4 py-2 text-sm font-medium text-white hover:bg-blue-700"
                >
                  Add
                </button>
              </div>
            </div>
          </div>
        )}

        {/* Type Modal */}
        {isTypeModalOpen && (
          <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/60">
            <div className="w-full max-w-md rounded-xl bg-white p-6 shadow-2xl">
              <h3 className="mb-4 text-lg font-semibold text-gray-800">Add New Type</h3>
              <input
                type="text"
                value={newTypeName}
                onChange={(e) => setNewTypeName(e.target.value)}
                placeholder="Enter type name"
                className="mb-4 w-full rounded-lg border border-gray-200 bg-gray-50 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
              />
              <div className="flex justify-end gap-2">
                <button
                  onClick={() => setIsTypeModalOpen(false)}
                  className="rounded-lg px-4 py-2 text-sm font-medium text-gray-600 hover:bg-gray-100"
                >
                  Cancel
                </button>
                <button
                  onClick={handleAddType}
                  className="rounded-lg bg-blue-600 px-4 py-2 text-sm font-medium text-white hover:bg-blue-700"
                >
                  Add
                </button>
              </div>
            </div>
          </div>
        )}
      </div>

      {/* Custom Styles */}
      <style jsx>{`
        input,
        select,
        button {
          transition: all 0.2s ease-in-out;
        }
        button:hover {
          transform: translateY(-1px);
        }
        input:focus,
        select:focus {
          box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.3);
        }
        ::-webkit-scrollbar {
          width: 8px;
        }
        ::-webkit-scrollbar-track {
          background: #f1f5f9;
        }
        ::-webkit-scrollbar-thumb {
          background: #4b5563;
          border-radius: 4px;
        }
      `}</style>
    </div>
  );
}